@import url(https://cdn.rawgit.com/konpa/devicon/df6431e323547add1b4cf45992913f15286456d3/devicon.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Poppins',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-family: 'Poppins', sans-serif; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Poppins', sans-serif;
  background-color: white;
}


/* MAIN */

.main-wrapper {
  min-height: 100vh;
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 19px;
  box-sizing: border-box;
  margin: 0 auto;
  box-shadow: 0 1px 5px #00000040, 0 0 50px #0000001a inset;
}

.flex-content {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.main {
  margin: 2px 7px 0;
  opacity: unset;
  -webkit-filter: none;
          filter: none;
}

.main.blur {
  opacity: 50%;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.main-content {
  margin: 0 0 17px;
}

/** Video **/

.video-wrapper {
  width: 100%;
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
  /* border: solid 4px #FF6D00; */
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


/* MENU */

.drawer-control svg {
  padding-left: 8px;
  padding-top: 3px;
  width: 33px;
  fill: #02b3e4;
}

.menu {
  width: 300px;
  margin-right: auto;
}

.menu-mobile {
  max-width: 170px;
  width: 100%;
  overflow: auto;
  position: absolute;
  margin-top: 2px; 
  -webkit-transform: translate(-180px,0); 
          transform: translate(-180px,0);
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  box-shadow: 0 1px 5px rgba(0,0,0,0.25), 0 0 50px rgba(0,0,0,0.1);
}

.menu-mobile.visible {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}


/* PROJECTS SLIDER */

.carousel {
  display: block;
}

.carousel-inner {
  overflow: hidden;
  padding: 5px 2px 5px 1px;
  display: block;
}

/** Project Image **/

.link-wrapper {
  width: 100%;
  display: inline-block;
}

.project-image {
  width: 99%;
  border-radius: 0;
  box-shadow: 0 10px 6px -6px #777;
  display: block;
  -webkit-transform: scale(0.91);
          transform: scale(0.91);
  -webkit-transition: -webkit-transform 150ms ease-in;
  transition: -webkit-transform 150ms ease-in;
  transition: transform 150ms ease-in;
  transition: transform 150ms ease-in, -webkit-transform 150ms ease-in;
}

.project-image:hover {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  box-shadow: 0 10px 6px -5px #777;
}

.project-image-selected {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}  

/** Controls **/

.previous-slide,
.next-slide {
  width: 48px;
  display: block;
  margin: 0 auto;
  cursor: pointer;
  fill: #e9c893;
  background-color: transparent;
  border: none;
  margin-bottom: 2px;
} 

.next-slide {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}


/* PROJECT DESCRIPTION */

.project-presentation {
  margin-bottom: 55px;
  width: 100%;
} 

/** Web Technologies **/

.web-technologies-icon {
  color: #ffbc8f;
  display: inline-block; 
  font-size: 41px;
  height: 50px;
  margin: 7px 5px 0 7px;
}

/** Description **/

.project-description-area {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.title-and-link {
  display: flex;
  flex-wrap: wrap;
}

.project-title {
  font-size: 19px;
  color: #464646;
  margin: 10px 10px 0;
  font-weight: 900;
}

.github-repo {
  font-size: 17px;
  text-decoration: none;
  color: #388dee;
  margin: 18px 0 0 25px;
}

.github-repo:hover {
  font-size: 18px;
}

.project-description {
  margin: 3px 2px 5px 10px;
  font-size: 15px;
  font-weight: 600;
  color: #757474;
  letter-spacing: 0.5px;
  line-height: 1.4;
  overflow-wrap: break-word;
}


/* SHORT BIO */

.personal-information {
  font-weight: 600;
}

.author-wrapper {
  display: flex;
  flex-direction: column;
} 

.orange-line {
  border: 2px solid #fdb28a;
  margin: 13px 0 0;
} 

/** Author's photo **/

.author-image-box {
  margin: 13px 27px 10px 10px;
}

.author-pic {
  width: 100%;
}

/** Information **/

.author-name {
  color: #464646;
  font-size: 24px;
  letter-spacing: 0.7px;
  margin-top: 0;
}

.personal-item-wrapper {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
}

.personal-item-title {
  color: #464646;
  margin: 0 1px;
  font-size: 17px;
}

.personal-item-icon {
  width: 25px;
  margin-right: 5px;
  margin-left: 2px;
  order: -1;
}

.personal-item {
  color: #388dee;
  margin: 0 0 15px;
  font-size: 15px;
}

.personal-item-link {
  color: #388dee;
  padding: 10px 4px 0 0;
  font-size: 15px;
  text-decoration: none;
}

.personal-item-link:hover {
  color: #469cff;
}


/* FEED READER SCREEN SHOT */

.feed-reader-screenShot {
  width: 100%;
}


/* FOOTER */

.site-footer {
  width: 100%;
  color: #7d97ad;
  text-align: center;
  font-size: 13px;
  border: none;
  background-color: #2b3946;
  position: absolute;
  bottom: 0;
}

.footer-text {
  margin: 0;
  color: #eaeaeb; 
}

/* MEDIA QUERY */

@media only screen and (min-width: 360px) and (max-width: 569px) {
  .menu-mobile {
    max-width: 185px;
    -webkit-transform: translate(-195px,0);
            transform: translate(-195px,0);
  }
}


@media only screen and (min-width: 570px) and (max-width: 973px) {
  .menu-mobile {
    max-width: 210px;
    -webkit-transform: translate(-220px,0);
            transform: translate(-220px,0);
  }
}


@media only screen and (max-width: 650px) {
    .author-image-box {
      margin: 0 auto;
      margin-top: 20px; 
    }
  
    .author-pic {
      max-width: 240px;
    }
    
    .personal-information {
      margin: 0 auto;
      word-break: break-all;
    }

    .personal-data {
      margin: 20px 0 0 18px;
    }
  
    .orange-line {
      border: none;
    }
  
    .personal-item,
    .personal-item-link {
      color: rgb(180, 180, 180);
    }

    .project-title {
      width: 65%;
    }

    .author-name {
      margin-left: 15px;
    }

    .github-repo {
      margin-top: 11px;
    }
  }


  @media only screen and (min-width: 651px) {
    .author-wrapper {
      flex-direction: row;
      padding-bottom: 15%;
    }
  }
  
  
  @media only screen and (min-width: 640px) and (max-width: 901px) {
    .web-technologies-icon {
      font-size: 50px;
      margin-right: 8px;
    }

    .github-repo {
      margin-top: 13px;
    }
  }
  
  
  @media only screen and (min-width: 902px) and (max-width: 1021px) {
    .author-name {
      font-size: 24px;
    }
   
    .web-technologies-icon {
      font-size: 52px;
      margin-right: 10px;
    }
    
    .project-title {
      font-size: 24px;
    }
    
    .project-description {
      font-size: 16px;  
    }
  }


  @media only screen and (max-width: 973px) { 
    .main {
      width: 100%;
    }
  }
  
  
  @media only screen and (min-width: 974px) {
    .main {
      width: 1000px;
      max-width: 67%;
      margin: 40px 50px 23px 0;
      padding-left: 27px;
    }

    .carousel {
      margin-top: 16px;
      max-width: 95%;
    }

    .video-wrapper {
      padding-bottom: 49.7%;
    }

    .project-presentation {
      margin-bottom: 20px;
    }
  }

  
  @media only screen and (min-width: 1022px) {
    .author-name {
      font-size: 28px;
    }
  
    .web-technologies-icon {
      font-size: 60px;
      margin-right: 11px;
    }
    
    .project-title {
      font-size: 25px;
    }
    
    .project-description {
      font-size: 17px;  
    }
  }


  @media only screen and (min-width: 1065px) {
    .main,
    .main.blur {
      margin-left: auto;
    }
  }
